'use client'

import { X } from 'lucide-react'

type Product = {
  id: number
  apcPN: string
  customer: string | null
  customerPN: string | null
  buildRev: string | null
  currentRev: string | null
  description: string | null
  fullPath: string | null
  item_type_name: string | null
  item_type_code?: string | null
  item_type_id: number
  createdAt: string
}

type Props = {
  product: Product
  onClose: () => void
}

export default function ProductView({ product, onClose }: Props) {
  const infoClassName = "w-full px-3 py-2 border border-slate-300 rounded-lg bg-slate-50 cursor-not-allowed text-sm"

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-2xl w-full max-w-3xl max-h-[90vh] flex flex-col">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-slate-200">
          <div>
            <h2 className="text-2xl font-bold text-slate-800">View Product</h2>
            <p className="text-sm text-slate-600 mt-1">Product information (read-only)</p>
          </div>
          <button
            onClick={onClose}
            className="p-2 hover:bg-slate-100 rounded-lg transition-colors"
          >
            <X size={24} className="text-slate-600" />
          </button>
        </div>

        {/* Content */}
        <div className="flex-1 overflow-auto p-6">
          <div className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  APC Part Number
                </label>
                <input
                  type="text"
                  value={product.apcPN}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Type
                </label>
                <input
                  type="text"
                  value={product.item_type_name || 'Unknown'}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Customer Part Number
                </label>
                <input
                  type="text"
                  value={product.customerPN || '-'}
                  readOnly
                  className={infoClassName}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Customer
                </label>
                <input
                  type="text"
                  value={product.customer || '-'}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Part Revision
                </label>
                <input
                  type="text"
                  value={product.currentRev || '-'}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Build Revision
                </label>
                <input
                  type="text"
                  value={product.buildRev || '-'}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div className="col-span-2">
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Description
                </label>
                <textarea
                  value={product.description || '-'}
                  readOnly
                  rows={3}
                  className={infoClassName}
                />
              </div>

              <div className="col-span-2">
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Full Path
                </label>
                <input
                  type="text"
                  value={product.fullPath || '-'}
                  readOnly
                  className={infoClassName}
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-1">
                  Created At
                </label>
                <input
                  type="text"
                  value={new Date(product.createdAt).toLocaleString()}
                  readOnly
                  className={infoClassName}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="flex items-center justify-end gap-3 p-6 border-t border-slate-200">
          <button
            onClick={onClose}
            className="px-4 py-2 text-slate-700 hover:bg-slate-100 rounded-lg transition-colors"
          >
            Close
          </button>
        </div>
      </div>
    </div>
  )
}